
import React from 'react';
import './Todo.css';


class Todo extends React.Component {
  constructor(props){
    super(props);
  
    this.state = {data: props.data}
   
  }

  static getDerivedStateFromProps(props, state) {
  
    return {data: props.data};
  }

   render(){

       let checkDone = '';
       if(!this.state.data.done)
       {

            checkDone=(
                <img src={process.env.PUBLIC_URL+"/asset/check.png"} alt="Add Task" width="30px" style={{cursor :'pointer'}}
                title="Hoan thanh"
                onClick= {()=>this.props.compateTask(this.state.data.id)}
                />
            )
       }
       return (
        <div className="Todo aligned">
            {checkDone}
            <img className={this.state.data.done ? 'nocheckDone' :''}
            src={process.env.PUBLIC_URL+"/asset/remove.png"} alt="Add Task" width="30px" style={{cursor :'pointer'}}
            title="Xoa di"
            onClick={()=>this.props.deleteTask(this.state.data.id)}
            />  
           <span className={this.state.data.done ? 'done' :''}>{this.state.data.name}</span>
        </div>
        

       )
   }
}

export default Todo;
