
import React from 'react';
import './TodoList.css';
import Todo from './Todo';

class TodoList extends React.Component {
  constructor(props){
    super(props)
    this.state={ name: props.name, taskname:'' , tasks:[]};
  }
  compateTask = (id)=>{
    this.state.tasks.forEach((value)=>{
      if(value.id === id)
      {
        value.done = true;
      }
    })
    this.setState(this.state.tasks)
    console.log(this.state)
  }
  deleteTask = (id)=>{
        const tasks = this.state.tasks.filter(a => a.id !==  id)

        this.setState({ tasks })
  }
  mytask = (event)=>{
    this.setState({taskname: event.target.value})
  }

  add_cart = ()=>{
    if(this.state.taskname)
    {
   
      const id = this.state.tasks.length;
      const name = this.state.taskname;
      this.state.tasks.push({id, name, done: false});
    
      this.setState({taskname:''})
    }else{
     return;
    }
  }
  render(){
    const html = 
      (
        <div className="App">
       
          <br/>
           {this.state.name}
          <div className="aligned">
            <img src={process.env.PUBLIC_URL + "/asset/plus.png"} alt="Add Task" width="30px" style={{cursor :'pointer'}}
            title="Bam de them"
            onClick={()=>this.add_cart()}
            />
            <input type="text" 
            value={this.state.taskname}
            onChange= {this.mytask}
            />
          </div>
            <ul style={{paddingLeft : '20px'}}>
              {this.state.tasks.map((value,index)=>{
             
                return <Todo 
                        key={index} 
                        data={value}
                        deleteTask={this.deleteTask}
                        compateTask={this.compateTask}
                     
                        />
              })}
            </ul>
       
        </div>
      );
      return html;
  } 
}

export default TodoList;
